































































import {
  defineComponent,
  computed,
  reactive,
  toRefs
} from '@vue/composition-api'

export default defineComponent({
  setup () {
    const state = reactive({
      show: false
    })

    const canShow = computed(() => {
      return getFiles()
    })

    const getFiles = () => {
      const files = require
        .context('../../../days', true, /.(png|jpg|jpeg)$/)
        .keys()
      const list = [] as Array<string>
      files.forEach(item => {
        const filename = item.split('/')[2]
        list.push(filename.split('-')[0])
        console.log(item, filename)
      })
      console.log(list)
      return hasEveryday(list)
    }

    const hasEveryday = (list: Array<string>) => {
      let num = 0
      for (let i = 1; i < 32; i++) {
        list.some(item => +item === i) && num++
      }
      document.documentElement.style.setProperty(
        '--top-height',
        -60 - ((num * 36) / 32) * 10 + 'px'
      )
      document.documentElement.style.setProperty(
        '--second-top-height',
        -70 - ((num * 36) / 32) * 10 + 'px'
      )
      if (num === 31 && list.some(item => item === 'LSY&RCC')) {
        document.documentElement.style.setProperty('--top-height', '-420px')
        document.documentElement.style.setProperty(
          '--second-top-height',
          '-430px'
        )
        return 2
      } else if (num === 31) {
        return 1
      }
      return 0
    }

    const handleClick = () => {
      state.show = true
    }

    return {
      ...toRefs(state),
      canShow,
      handleClick
    }
  }
})
